body {
	background: #384047;
	font-family: sans-serif;
	font-size: 10px
}
.register-form {
	background: #fff;
	padding: 4em 4em 2em;
	max-width: 400px;
	margin: 50px auto 0;
	box-shadow: 0 0 1em #222;
	border-radius: 2px;
	
	
}

.register-form h2 {
    margin:0 0 50px 0;
    padding:10px;
    text-align:center;
    font-size:30px;
    color:darken(#e5e5e5, 50%);
    border-bottom:solid 1px #e5e5e5;
}

.register-form p {
    margin: 0 0 3em 0;
    position: relative;
}

.register-form input {
    display: block;
    box-sizing: border-box;
    width: 100%;
    outline: none;
    margin:0;
}

.register-form input[type="text"],
.register-form input[type="password"] {
    background: #fff;
    border: 1px solid #dbdbdb;
    font-size: 1.6em;
    padding: .8em .5em;
    border-radius: 2px;
}

.register-form input[type="text"]:focus,
.register-form input[type="password"]:focus {
    background: #fff
}

.register-form span {
    display:block;
    background: #F9A5A5;
    padding: 2px 5px;
    color: #666;
}

.register-form input[type="submit"] {
    background: rgba(148,186,101,0.7);;
    box-shadow: 0 3px 0 0 darken(rgba(148,186,101,0.7), 10%);
    border-radius: 2px;
    border: none;
    color: #fff;
    cursor: pointer;
    display: block;
    font-size: 2em;
    line-height: 1.6em;
    margin: 2em 0 0;
    outline: none;
    padding: .8em 0;
    text-shadow: 0 1px #68B25B;
}

.register-form input[type="submit"]:hover {
    background: rgba(148,175,101,1);
    text-shadow:0 1px 3px darken(rgba(148,186,101,0.7), 30%);
}

.register-form input[type="submit"]:hover {
    color: pink;
}

.register-form label {
    position: absolute;
    left: 8px;
    top: 12px;
    color: #999;
    font-size: 16px;
    display: inline-block;
    padding: 4px 10px;
    font-weight: 400;
    background-color: rgba(255,255,255,0);
    transition: (color .3s, top .3s, background-color .8s);
    
}

.floatLabel {
    top: -11px;
    background-color: rgba(255,255,255,0.8);
    font-size: 14px;
}